<template>
  <base-section id="home-hero" class="py-6 py-md-12">
    <v-responsive min-height="30vh">
      <v-container>
        <v-row align="center">
          <v-col cols="12">
            <v-responsive>
              <v-carousel
                :progress="true"
                :continuous="true"
                :show-arrows="false"
                vertical-delimiters="right"
